import React from 'react';

export default function Header() {
    return (
        <>
            <header className="hero-area">
                <div className="overlay">
                    <span></span>
                    <span></span>
                </div>
                <div className="navbar-area">
                    <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <nav className="navbar navbar-expand-lg">
                                <a className="navbar-brand" href="index.html">
                                <img className="brand-logo" src="images/farm/logo.png" alt="Logo" />
                                </a>
                                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="toggler-icon"></span>
                                <span className="toggler-icon"></span>
                                <span className="toggler-icon"></span>
                                </button>
                                <div className="collapse navbar-collapse sub-menu-bar" id="navbarSupportedContent">
                                <ul id="nav" className="navbar-nav ml-auto">
                                    <li className="nav-item active">
                                        <a className="page-scroll" href="#home">Home</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="page-scroll" href="#services">About</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="page-scroll" href="#features">Services</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="page-scroll" href="#contact">Contact</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="btn btn-border page-scroll" href="#" rel="nofollow">Sign In</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="btn mt-1 text-light btn-common page-scroll" href="#" rel="nofollow">Sign Up</a>
                                    </li>
                                </ul>
                                </div>
                            </nav>
                        </div>
                    </div>
                    </div>
                </div>
                <div id="home">
                    <div className="container">
                    <div className="row space-100">
                        <div className="col-lg-6 col-md-12 col-xs-12">
                            <div className="contents">
                                <h2 className="head-title text-dark">Join Over 12000+ Members.</h2>
                                <p className="text-dark mb-2">Farm4Me is the pioneers of contract farming in Nigeria.</p>
                                <p className="text-dark mb-2">Our Clients and Members engage our services as "Farm Managers" to set up and manage farms for them. We ship either the produce or sale the produce and return their principal and profits.</p>
                                <p className="text-dark mb-2">Contract Farming is an investment product of Jamis Microfinance Bank Limited.</p>
                                <p className="text-dark mb-2">Since inception in March 2019 till now, we have consistently paid our Investors their dividends without fail.</p>
                                <p className="text-dark mb-2">Farm4Me's contract farming is powered by CBN licensed Jamis Microfinance Bank Limited.</p>
                            </div>
                        </div>
                        <div className="col-lg-6 col-md-12 col-xs-12 p-0 bg-side bg-side-buttom">
                            <div className="intro-img px-3">
                                <img className="lead" src="images/farm/lead.png" alt="" />
                            </div>
                            <div className="my-5 px-3">
                                <p className="text-left text-light">All our farms are Insured by Leadway Assurance Limited.</p>
                                <p className="text-left text-light">The farm insurance policies cover natural disasters and pest invasions. Not your Investment Principal.</p>
                            </div>
                        </div>
                    </div>
                    </div>
                </div>
            </header>
        </>
    )
}
